<template>
  <panel style="margin: 35px auto; text-align: center; padding: 20px;">
    <h3>Withdraw ROBUX!</h3><br>
    <input type="number" placeholder="Amount" v-model="withdrawAmount">
    <button @click="withdraw()">Withdraw</button>
  </panel>
</template>

<script>
import panel from '../panel'

export default {
    name: 'Payout',
    components: {
      panel
    },
    data() {
      return {
        withdrawAmount: undefined
      }
    },
    methods: {
      withdraw() {
        fetch(`${this.$apiHostname}/api/payout`, {
                method: 'POST',
                headers: {
                    'content-type': 'application/json'
                },
                body: JSON.stringify({ name: localStorage.account, amount: this.withdrawAmount }),
                mode: 'cors'
                })
                .then(() => {
                    this.withdrawAmount = undefined
                })
      }
    }
}
</script>

